import React, { Component } from 'react'
import { Collapse } from 'antd'
import IconItem from '@/component/IconItem'

const Panel = Collapse.Panel;

class Palette extends Component {
  render() {
    return (
      <Collapse>
        <Panel header="Workflow Control" key="1">
          <Collapse defaultActiveKey="1">
            <Panel header="Event" key="1">
              <div className="icon-tool-bar">
                <IconItem
                  key="1"
                  id="1"
                  name="Start"
                  class_name="blendrdb2hivesqoop"
                  type="SP"
                />
              </div>
            </Panel>
            <Panel header="Flow" key="1-1">
            </Panel>
            <Panel header="Task" key="1-2">
            </Panel>
          </Collapse>
        </Panel>
        <Panel header="Data Preparation" key="2">
          <Collapse defaultActiveKey="2">
            <Panel header="Blending" key="2">
            </Panel>
          </Collapse>
        </Panel>
      </Collapse>
    );
  }
}

export default Palette;


